	        justify-content: flex-start;
	position: relative;
	width: 50px;
  height: 50px;
  padding: 50px;
	-webkit-transform: scale(0);
	        transform: scale(0);
	border-radius: 50%;
	opacity: 0;
	overflow: hidden;
	-webkit-animation: scale-in .3s ease-out forwards,
		expand .35s .25s ease-out forwards;
	        animation: scale-in .3s ease-out forwards,
		expand .35s .25s ease-out forwards;
}

